body {
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

body, input, button {
  font-family: sans-serif;
  font-size: max(12pt, 5vh);
}

.timer-table {
  border-collapse: collapse;
  border: 1px solid #000;
  width: 100%;
  min-height: 100%;
  position: absolute;
}

.timer-table tr {
  max-height: 5vh;
}

.timer-table td, .timer-table th {
  border: 1px solid #000;
  width: 0%;
  height: 0%;
  position: relative;
}

.timer-table td {
  text-align: right;
}

.timer-table th {
  text-align: center;
  max-height: 24px;
}

.timer-table td input {
  text-align: right;
  padding: none;
  border: none;
  position: absolute;
  inset: 0;
}

.timer-table td button {
  background-color: #fff;
}

.timer-table td input::placeholder {
  color: #888;
}

.timer-table .button-group {
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 0;
}

.timer-table .button-group button {
  border: none;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 100%;
}

